.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 10;
    &._scroll {} }

.menu {
    &__list {

        @media (min-width: 768px) {
            display: flex; } }
    &__body {
        position: fixed;
        z-index: 3;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
        visibility: hidden;
        transition: all 0.5s 0.5s;
        display: block;
        opacity: 0;
        visibility: hidden;
        &._active {
            opacity: 1;
            visibility: visible;
            overflow-y: scroll;
            overflow-x: hidden;
            transition: all 1s; } }

    &__link {} }

//====================================================================================================

//Burger
.icon-menu {
    display: block;
    position: relative;
    width: 30px;
    height: 22px;
    cursor: pointer;
    z-index: 11;
    @media (max-width: 479px) {
        width: 29px; }

    span {
        transition: all 0.3s ease 0s;
        top: calc(50% - 2px);
        left: 0px;
        position: absolute;
        width: 100%;
        height: 3px;
        background-color: #fff;
        &:first-child {
            top: 0px; }

        &:last-child {
            top: auto;
            bottom: 0px; } }

    &._active {
        span {
            transform: scale(0);
            &:first-child {
                transform: rotate(-45deg);
                top: calc(50% - 2px); }

            &:last-child {
                transform: rotate(45deg);
                bottom: calc(50% - 1px); } } } }
