.lg-actions .lg-next,
.lg-actions .lg-prev {
    cursor: pointer;
    position: absolute;
    width: 20px;
    height: 36px;
    top: 50%;
    transform: translate(0px, -50%);
    z-index: 1080;
    // @extend %arrow-slider;
    background-size: 100%;
    background-repeat: no-repeat; }

.lg-actions .lg-next.disabled,
.lg-actions .lg-prev.disabled {
    pointer-events: none;
    opacity: 0.5; }

.lg-actions .lg-next {
    right: 20px;
    @media (max-width: 479px) {
        right: 5px; } }

.lg-actions .lg-prev {
    left: 20px;
    transform: translate(0px, -50%) matrix(-1, 0, 0, 1, 0, 0);
    @media (max-width: 479px) {
        left: 5px; } }

@keyframes lg-right-end {
    0% {
        left: 0; }

    50% {
        left: -30px; }

    100% {
        left: 0; } }

.lg-toolbar {
    z-index: 1082;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;


    & .lg-icon {
        color: #999;
        cursor: pointer;
        float: right;
        font-size: 24px;
        height: 47px;
        line-height: 27px;
        padding: 10px 0;
        text-align: center;
        width: 50px;
        text-decoration: none !important;
        outline: medium none;
        transition: color 0.2s linear; }


    & .lg-icon:hover {
        color: #fff; }


    & .lg-close {
        display: flex;
        justify-content: center;
        align-items: center; }
    &:before,
    &:after {
        content: "";
        width: 20px;
        height: 2px;
        background-color: #fff;
        position: absolute; }

    &:before {
        transform: rotate(45deg); }

    &:after {
        transform: rotate(-45deg); }


    & .lg-download {
        display: none; }


    & .lg-download:after {}
 }    //content: "\e0f2";

.lg-sub-html {
    background-color: rgba(0, 0, 0, 0.45);
    bottom: 0;
    color: #eee;
    font-size: 16px;
    left: 0;
    padding: 10px 40px;
    position: fixed;
    right: 0;
    text-align: center;
    z-index: 1080;


    & h4 {
        margin: 0;
        font-size: 13px;
        font-weight: bold; }


    & p {
        font-size: 12px;
        margin: 5px 0 0; } }

#lg-counter {
    color: #999;
    display: inline-block;
    font-size: 16px;
    padding-left: 20px;
    padding-top: 12px;
    vertical-align: middle; }

.lg-toolbar,
.lg-prev,
.lg-next {
    opacity: 1;
    transition: transform 0.35s cubic-bezier(0, 0, 0.25, 1) 0s, opacity 0.35s cubic-bezier(0, 0, 0.25, 1) 0s, color 0.2s linear; }

.lg-hide-items .lg-prev {
    opacity: 0;
    transform: translate3d(-10px, 0, 0); }

.lg-hide-items .lg-next {
    opacity: 0;
    transform: translate3d(10px, 0, 0); }

.lg-hide-items .lg-toolbar {
    opacity: 0;
    transform: translate3d(0, -10px, 0); }

body:not(.lg-from-hash) .lg-outer.lg-start-zoom .lg-object {
    transform: scale3d(0.5, 0.5, 0.5);
    opacity: 0;
    transition: transform 250ms cubic-bezier(0, 0, 0.25, 1) 0s, opacity 250ms cubic-bezier(0, 0, 0.25, 1) !important;
    transform-origin: 50% 50%; }

body:not(.lg-from-hash) .lg-outer.lg-start-zoom .lg-item.lg-complete .lg-object {
    transform: scale3d(1, 1, 1);
    opacity: 1; }

.lg-outer .lg-thumb-outer {
    background-color: #0d0a0a;
    bottom: 0;
    position: absolute;
    width: 100%;
    z-index: 1080;
    max-height: 350px;
    transform: translate3d(0, 100%, 0);
    transition: transform 0.25s cubic-bezier(0, 0, 0.25, 1) 0s;


    &.lg-grab .lg-thumb-item {
        cursor: -webkit-grab;
        cursor: -moz-grab;
        cursor: -o-grab;
        cursor: -ms-grab;
        cursor: grab; }


    &.lg-grabbing .lg-thumb-item {
        cursor: move;
        cursor: -webkit-grabbing;
        cursor: -moz-grabbing;
        cursor: -o-grabbing;
        cursor: -ms-grabbing;
        cursor: grabbing; }


    &.lg-dragging .lg-thumb {
        transition-duration: 0s !important; } }

.lg-outer.lg-thumb-open .lg-thumb-outer {
    transform: translate3d(0, 0%, 0); }

.lg-outer .lg-thumb {
    padding: 10px 0;
    height: 100%;
    margin-bottom: -5px;


    &-item {
        border-radius: 5px;
        cursor: pointer;
        float: left;
        overflow: hidden;
        height: 100%;
        border: 2px solid #fff;
        border-radius: 4px;
        margin-bottom: 5px; } }

@media (min-width: 1025px) {
    .lg-outer .lg-thumb-item {
        transition: border-color 0.25s ease; } }

.lg-outer .lg-thumb-item.active,
.lg-outer .lg-thumb-item:hover {
    border-color: #a90707; }

.lg-outer .lg-thumb-item img {
    width: 100%;
    height: 100%;
    object-fit: cover; }

.lg-outer.lg-has-thumb .lg-item {
    padding-bottom: 120px; }

.lg-outer.lg-can-toggle .lg-item {
    padding-bottom: 0; }

.lg-outer.lg-pull-caption-up .lg-sub-html {
    transition: bottom 0.25s ease; }

.lg-outer.lg-pull-caption-up.lg-thumb-open .lg-sub-html {
    bottom: 100px; }

.lg-outer .lg-toggle-thumb {
    background-color: #0d0a0a;
    border-radius: 2px 2px 0 0;
    color: #999;
    cursor: pointer;
    font-size: 24px;
    height: 39px;
    line-height: 27px;
    padding: 5px 0;
    position: absolute;
    right: 20px;
    text-align: center;
    top: -39px;
    width: 50px;


    &:after {
        content: "\e1ff"; }


    &:hover {
        color: #fff; } }

.lg-outer.lg-css3.lg-zoom-dragging .lg-item.lg-complete.lg-zoomable .lg-img-wrap,
.lg-outer.lg-css3.lg-zoom-dragging .lg-item.lg-complete.lg-zoomable .lg-image {
    transition-duration: 0s; }

.lg-outer.lg-use-transition-for-zoom .lg-item.lg-complete.lg-zoomable .lg-img-wrap {
    transition: transform 0.3s cubic-bezier(0, 0, 0.25, 1) 0s; }

.lg-outer.lg-use-left-for-zoom .lg-item.lg-complete.lg-zoomable .lg-img-wrap {
    transition: left 0.3s cubic-bezier(0, 0, 0.25, 1) 0s, top 0.3s cubic-bezier(0, 0, 0.25, 1) 0s; }

.lg-outer .lg-item.lg-complete.lg-zoomable .lg-img-wrap {
    transform: translate3d(0, 0, 0);
    backface-visibility: hidden; }

.lg-outer .lg-item.lg-complete.lg-zoomable .lg-image {
    transform: scale3d(1, 1, 1);
    transition: transform 0.3s cubic-bezier(0, 0, 0.25, 1) 0s, opacity 0.15s !important;
    transform-origin: 0 0;
    backface-visibility: hidden; }

#lg-zoom-in:after {
    content: "\e311"; }

#lg-actual-size {
    font-size: 20px;


    &:after {
        content: "\e033"; } }

#lg-zoom-out {
    opacity: 0.5;
    pointer-events: none;


    &:after {
        content: "\e312"; }


    .lg-zoomed & {
        opacity: 1;
        pointer-events: auto; } }

.lg-outer .lg-caret {
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-top: 10px dashed;
    bottom: -10px;
    display: inline-block;
    height: 0;
    left: 50%;
    margin-left: -5px;
    position: absolute;
    vertical-align: middle;
    width: 0; }

.lg-fullscreen:after {
    content: "\e20c"; }

.lg-fullscreen-on .lg-fullscreen:after {
    content: "\e20d"; }

.group {
    *zoom: 1;


    &:before,

    &:after {
        display: table;
        content: "";
        line-height: 0; }


    &:after {
        clear: both; } }

.lg-outer {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1050;
    opacity: 0;
    transition: opacity 0.15s ease 0s;


    & * {
        box-sizing: border-box; }


    &.lg-visible {
        opacity: 1; }


    &.lg-css3 .lg-item.lg-prev-slide,

    &.lg-css3 .lg-item.lg-next-slide,

    &.lg-css3 .lg-item.lg-current {
        transition-duration: inherit !important;
        transition-timing-function: inherit !important; }


    &.lg-css3.lg-dragging .lg-item.lg-prev-slide,

    &.lg-css3.lg-dragging .lg-item.lg-next-slide,

    &.lg-css3.lg-dragging .lg-item.lg-current {
        opacity: 1; }


    &.lg-grab img.lg-object {
        cursor: -webkit-grab;
        cursor: -moz-grab;
        cursor: -o-grab;
        cursor: -ms-grab;
        cursor: grab; }


    &.lg-grabbing img.lg-object {
        cursor: move;
        cursor: -webkit-grabbing;
        cursor: -moz-grabbing;
        cursor: -o-grabbing;
        cursor: -ms-grabbing;
        cursor: grabbing; }


    & .lg {
        height: 100%;
        width: 100%;
        position: relative;
        overflow: hidden;
        margin-left: auto;
        margin-right: auto;
        max-width: 100%;
        max-height: 100%; }


    & .lg-inner {
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        white-space: nowrap; }


    & .lg-item {}
    // background: url("../img/icons/loading.gif") no-repeat scroll center center transparent;
    //  display: none !important


    &.lg-css3 .lg-prev-slide,

    &.lg-css3 .lg-current,

    &.lg-css3 .lg-next-slide {
        display: inline-block !important; }


    &.lg-css .lg-current {
        display: inline-block !important; }


    & .lg-item,

    & .lg-img-wrap {
        display: inline-block;
        text-align: center;
        position: absolute;
        width: 100%;
        height: 100%; }


    & .lg-item:before,

    & .lg-img-wrap:before {
        content: "";
        display: inline-block;
        height: 50%;
        width: 1px;
        margin-right: -1px; }


    & .lg-img-wrap {
        position: absolute;
        padding: 0 5px;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0; }


    & .lg-item.lg-complete {
        background-image: none; }


    & .lg-item.lg-current {
        z-index: 1060; }


    & .lg-image {
        display: inline-block;
        vertical-align: middle;
        max-width: 100%;
        max-height: 100%;
        width: auto !important;
        height: auto !important; }


    &.lg-show-after-load .lg-item .lg-object,

    &.lg-show-after-load .lg-item .lg-video-play {
