._tabs-wrapper {
  position: relative;
}

._tabs-item {
  cursor: pointer;
}

._tabs-block {
  height: 0;
  transform: translate(25%, 0);
  transition: height 0.6s, transform 0.6s ease, visibility 0.6s, opacity 0.6s;
  visibility: hidden;
  opacity: 0;
  position: absolute;
  top: 0;
  z-index: 4;
}

._tabs-block._active {
  z-index: 5;
  transform: translate(0, 0);
  top: 0;
  position: relative;
  opacity: 1;
  visibility: visible;
  transform: scale(1);
  transition: position 0.5s 0.1s, transform 1s 0.1s ease, visibility 0.1s 0.1s, opacity 1s 0.1s;
  height: auto;
}

._tabs__block {
  display: none;
}

._tabs__block._open {
  display: block;
}

._tabs__block {
  display: none;
}

._tabs__block._open {
  display: block;
}

.btn-wrapper {
  display: flex;
  justify-content: center;
}

._btn {
  height: 50px;
  max-width: 340px;
  width: 100%;
  padding: 0 10px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
  color: #fff;
  white-space: nowrap;
  transition: color 0.5s;
}

._btn:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background: blue;
}

._btn span {
  position: absolute;
  display: block;
  width: 0;
  height: 0;
  border-radius: 50%;
  background-color: #053a97;
  transition: width 0.5s, height 0.5s;
  transform: translate(-50%, -50%);
  z-index: -1;
}

._btn svg {
  flex-shrink: 0;
  position: relative;
  z-index: 2;
  transition: transform 0.7s;
}

._btn svg path {
  transition: fill 0.7s;
}

._btn:active {
  transform: translateY(0.2rem);
}

.back-to-top {
  position: fixed;
  right: -60px;
  background: #1f60d1;
  border-radius: 50%;
  bottom: 15px;
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 8;
  visibility: hidden;
  transition: right 0.7s, visibility 0.7s, background 0.7s;
}

.back-to-top:before {
  content: "";
  width: 14px;
  height: 14px;
  border: solid #fff;
  border-radius: 3px;
  border-width: 3px 3px 0 0;
  transform: rotate(-45deg);
  position: relative;
  top: 3px;
}

.back-to-top:active:before {
  top: 2px;
}

.back-to-top._show {
  visibility: visible;
  right: 5px;
}

.anim {
  transform: translate(0, 40px);
  opacity: 0;
  transition: transform 0.8s 0.3s, opacity 0.8s 0.3s;
}

.anim._active {
  transform: translate(0, 0);
  opacity: 1;
  transition: transform 0.5s 0.1s, opacity 0.5s 0.1s;
}

.underline {
  position: relative;
}

.underline:before {
  content: "";
  width: 100%;
  height: 2px;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scaleX(0);
  transition: 0.5s transform;
  background-color: #fff;
}

@keyframes shake {
  0%, 50%, 100% {
    transform: rotate(0deg);
  }

  10%, 30% {
    transform: rotate(-10deg);
  }

  20%, 40% {
    transform: rotate(10deg);
  }
}

